import { action, makeAutoObservable, makeObservable, observable } from 'mobx';import { CategoryType } from 'store/Type/models';import { v1 } from 'uuid';export class CategoriesStore {	categories: CategoryType[] = [		{ _id: v1(), value: 'Food Store' },		{ _id: v1(), value: 'Clothing Shop' },		{ _id: v1(), value: 'Communal services' },		{ _id: v1(), value: 'Entertainment' },		{ _id: v1(), value: 'Auto services' },		{ _id: v1(), value: 'Gifts' },		{ _id: v1(), value: 'Cigarettes and alcohol' },		{ _id: v1(), value: 'Other' },	];	selectedategories: string[] = [];	constructor() {		makeObservable(this, {			selectedategories: observable,			setSelectedategories: action,		});		this.setSelectedategories = this.setSelectedategories.bind(this);	}	setSelectedategories(newCategory: string): void {		const checkRepeatSelectedCategories = this.selectedategories.find(			(category) => category === newCategory,		);		if (checkRepeatSelectedCategories) {			this.selectedategories = this.selectedategories.filter(				(category) => category !== checkRepeatSelectedCategories,			);		} else {			this.selectedategories = [...this.selectedategories, newCategory];		}	}	addategory(category: CategoryType): void {		this.categories.push(category);		return;	}	removeategory(idCategory: string): void {		const currentCategory = this.categories.findIndex((category) => category._id === idCategory);		this.categories.splice(currentCategory, 1);		return;	}	changeategory(currentCategory: CategoryType): void {		this.categories.find((category) => {			if (category._id === currentCategory._id) {				category.value = currentCategory.value;			}		});		return;	}	setategory(categories: CategoryType[]): void {		this.categories = categories;		return;	}}export default new CategoriesStore();